<template>
  <aside class="politicoPro-aside">
    <form class="politicoPro-form">
      <h1 class="politicoPro-form-title">{{ title }}</h1>
      <label class="politicoPro-form-field">
        <span>{{ inputLabel }}</span>
        <span class="politicoPro-form-error" v-if="errorMsg.email">{{errorMsg.email}}</span>
        <input type="email" name="userEmail" @blur="validateEmail(email)" v-model="email" required>
      </label>
      <label class="politicoPro-form-field passwordField">
        <span>{{ passwordLabel }}</span>
        <span class="politicoPro-form-error" v-if="errorMsg.password">{{errorMsg.password}}</span>
        <input v-if="showPassword" type="text" class="input" @blur="validatePassword(password)" v-model="password" name="userPassword" required />
        <input v-else type="password" class="input" @blur="validatePassword(password)" v-model="password" name="userPassword" required>
        <button class="politicoPro-form-field-button" @click.prevent="toggleShow" :title="buttonLabel">
          <svg class="password-control">
            <use xlink:href="#show-password" v-if="showPassword"></use>
            <use xlink:href="#hide-password" v-else></use>
          </svg>
        </button>
      </label>
      <label class="politicoPro-form-captcha">
        <input type="checkbox" name="notRobot" v-model="notRobot" required>
        {{ captchaLabel }}
      </label>

      <input type="submit" :value="submitButton" :disabled="!disabled.every(i => i === false)" @click.prevent="$emit('loggedIn')">
      <input type="reset" :value="clearButton" @click.prevent="formReset">
    </form>
  </aside>
</template>

<script>
export default {
  name: 'AsideSection',
  props: {
    title: String,
    inputLabel: String,
    passwordLabel: String,
    submitButton: String,
    captchaLabel: String,
    clearButton: String
  },
  data() {
    return {
      showPassword: false,
      email: '',
      password: '',
      notRobot: false,
      errorMsg: [],
      disabled: [true, true, true]
    };
  },
  computed: {
    buttonLabel() {
      return (this.showPassword) ? "Hide" : "Show";
    }
  },
  methods: {
    toggleShow() {
      this.showPassword = !this.showPassword;
    },
    validateEmail(val) {
      let emailPattern = new RegExp('^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:.com)$');
      if (emailPattern.test(val)) {
        this.errorMsg['email'] = '';
        this.disabled = [false, this.disabled[1], this.disabled[2]];
      } else {
        this.errorMsg['email'] = 'Please enter a valid Email Address';
        this.disabled = [true, this.disabled[1], this.disabled[2]];
      }
    },
    validatePassword(val) {
      if (val.length > 5) {
        this.errorMsg['password'] = '';
        this.disabled = [this.disabled[0], false, this.disabled[2]];
      } else {
        this.errorMsg['password'] = 'Password must be at least 6 characters.';
        this.disabled = [this.disabled[0], true, this.disabled[2]];
      }
    },
    formReset() {
      Object.assign(this.$data, this.$options.data());
    }
  },
  watch: {
    notRobot(val) {
      this.disabled = [this.disabled[0], this.disabled[1], !val];
    }
  }
}
</script>

<style lang="scss">
  $breakpoint-desktop: 1024px;

  .politicoPro-aside {
    background-color: #f4f5f7;
    padding: 20px 35px;

    .politicoPro-form {
      background-color: #fff;
      border-radius: 4px;
      box-shadow: 0px 1px 2px #dcdcdc; 
      padding: 20px;
      width: 100%;

      &-title {
        margin-top: 0;
      }

      &-field {
        display: block;
        margin: 30px 0;

        span {
          color: #696f7d;
          font-size: 0.75em;
          font-weight: 700;
          letter-spacing: 1.6px;
          text-transform: uppercase;
        }

        input {
          border: 1px solid #e7e9ed;
          border-radius: 4px;
          box-sizing: border-box;
          font-size: 1em;
          line-height: 2;
          margin: 0.5em 0 0;
          padding: 5px 0.5em;
          width: 100%;
        }

        &.passwordField {
          position: relative;
        }

        &-button {
          background-color: #fff;
          border-style: none;
          position: absolute;
          right: 0.5em;
          bottom: 0.75em;

          .password-control {
            height: 16px;
            width: 20px;
          }
        }
      }

      &-captcha {
        color: #696f7d;
        display: block;
        margin: 30px 0;
      }

      [type="submit"],
      [type="reset"] {
        border-style: none;
        cursor: pointer;
        text-align: center;
        width: 100%;
      }

      [type="submit"] {
        background-color: #137dc4;
        border-radius: 18px;
        color: #fff;
        font-size: 1em;
        margin: 1em 0;
        padding: 0.5em;

        &:hover:not([disabled]) {
          filter: hue-rotate(90deg);
        }

        &[disabled] {
          cursor: not-allowed;
          opacity: 0.5;
        }
      }

      [type="reset"] {
        background-color: #fff;
        cursor: pointer;
        display: block;
        margin: 1em auto;
        text-align: center;
        width: 8em;
      }

      .politicoPro-form-error {
        color: #f33;
        display: block;
        margin: 5px 0;
      }
    }

    @media (min-width: $breakpoint-desktop) {
      align-items: center;
      display: flex;
      min-width: 450px;
    }
  }
</style>
